import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';

import axios from "axios";
import Form from 'react-bootstrap/Form'
import Footer from "../Footer";
import { withRouter } from 'react-router-dom';
class Login extends Component {
    //state = {  }

    state = { isLoginShow: false, isRegShow: false, firstName: '', lastName: '', email: '', mobileNo: '', userMobileNo: '', userPassword: '' }
    state = { item: [], id: '', bookingId: '', mobileNo: '', otp: '', isOtpSent: 'N', error: '', userPassword: '' };
    constructor(props) {
        super(props);
        //console.log("++pickup**********==="+JSON.stringify(this.props));
        this.setState({ isOtpSent: 'N' });
    }

    componentDidMount() {
        let userId = localStorage.getItem("userId");
        if (userId > 0) {
            window.location.href = "/Home";
        }
        this.setState({ bookingId: this.props.match.params.orderId });
        //this.setState({item:this.props.match.params.data});
    }
    async register() {
        this.setState({ error: '' });

        if (this.state.firstName == "" || this.state.firstName == undefined) {
            this.setState({ error: "Please enter first name" });
            return false;
        }
        if (this.state.lastName == "" || this.state.lastName == undefined) {
            this.setState({ error: "Please enter last name" });
            return false;
        }
        if (this.state.userMobileNo == "" || this.state.userMobileNo.length != 10) {
            this.setState({ error: "Invalid mobile number" });
            return false;
        }
        if (this.state.email == "" || this.state.email == undefined) {
            this.setState({ error: "Please enter email" });
            return false;
        }
        if (this.state.userPassword == "" || this.state.userPassword == undefined) {
            this.setState({ error: "Please enter Password" });
            return false;
        }

        let urlData = "&mobileNo=" + this.state.userMobileNo + "&fname=" + this.state.firstName + "&lname=" + this.state.lastName + "&email=" + this.state.email + "&userPassword=" + this.state.userPassword + "&type=user";
        const headers = { 'Content-Type': 'application/json' }
        //  let url="https://c77b3hr0m0.execute-api.ap-south-1.amazonaws.com/v1/prayag-verifyOtp?mobileNo="+this.state.mobileNo+"&otp="+this.state.otp;
        //const response = await fetch(process.env.REACT_APP_APIURL+'user/register?'+urlData, { headers });
        try {
            const resultpay = await axios.post(process.env.REACT_APP_APIURL + "user/create_user", urlData);
            console.log(resultpay.data + "resultpay:" + JSON.stringify(resultpay.data));
            if (resultpay.data) {
                this.setState({ error: 'Register successfully' });
                this.setState({ userMobileNo: "" });
                this.setState({ firstName: "" });
                this.setState({ lastName: "" });
                this.setState({ email: "" });
            } else {
                this.setState({ isOtpSent: 'N' });
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

    }
    setFirstName = (fname) => {
        this.setState({ firstName: fname.target.value });
    }

    setLastName = (lname) => {
        this.setState({ lastName: lname.target.value });
    }

    setEmail = (email) => {
        this.setState({ email: email.target.value });
    }
    setUserMobileNo = (rmobile) => {

        this.setState({ userMobileNo: rmobile.target.value });
    }
    setUserPassword = (userPassword) => {
        this.setState({ userPassword: userPassword.target.value });
    }
    render() {

        return (

            <div>
                <div style={{ width: "50vh", left: '0', right: '0', margin: 'auto', borderRadius: '2%' }}>
                    <section id="pricing" className="pricing">
                        <div>
                            <div className="col-12" style={{ textAlign: 'center', marginBottom: "20px" }}><img src="/logo_cab.png" style={{ width: 200, padding: 5, borderRadius: "12px" }}></img></div>
                            <div className="col-12" style={{ textAlign: 'center' }}>
                                <h3>Wel-Come To VishwaCarRental</h3>
                            </div>
                            <div className="col-12" style={{ color: 'red' }}>
                                {this.state.error}
                            </div>
                            <div className="col-12">
                                <Form.Group controlId="formBasicEmail" >
                                    <Form.Label>First Name</Form.Label>
                                    <Form.Control name="firstName" value={this.state.firstName} type="text" placeholder="First Name" onChange={this.setFirstName} />
                                </Form.Group>
                            </div>
                            <div className="col-12">
                                <Form.Group controlId="formBasicEmail" >
                                    <Form.Label>Last Name</Form.Label>
                                    <Form.Control name="lastName" value={this.state.lastName} type="text" placeholder="Last Name" onChange={this.setLastName} />
                                </Form.Group>
                            </div>

                            <div className="col-12">
                                <Form.Group controlId="formBasicEmail" >
                                    <Form.Label>Mobile No</Form.Label>
                                    <Form.Control name="userMobileNo" value={this.state.userMobileNo} type="number" placeholder="Mobile No" onChange={this.setUserMobileNo} />
                                </Form.Group>
                            </div>
                            <div className="col-12">
                                <Form.Group controlId="formBasicEmail" >
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="email" value={this.state.email} type="text" placeholder="Email" onChange={this.setEmail} />
                                </Form.Group>
                            </div>
                            <div className="col-12">
                                <Form.Group controlId="formBasicEmail" >
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control name="userPassword" value={this.state.userPassword} type="text" placeholder="Password" onChange={this.setUserPassword} />
                                </Form.Group>
                            </div>


                            <div className="col-12">
                                <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                    <Button variant="primary" type="button" onClick={this.register.bind(this)}>
                                        Register
                                    </Button>
                                </Form.Group>
                                <Form.Group controlId="formBasicEmail" style={{ float: 'left', color: 'white' }}>
                                    <Button variant="primary" type="button">
                                        <a style={{ color: 'white' }} href="/Home" ><span>Home</span></a>
                                    </Button>
                                </Form.Group>
                            </div>

                        </div>

                    </section>


                </div>
                <Footer />
            </div>
        );
    }
}

export default withRouter(Login);